<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-dialog
      v-model="approveResultsDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Confirm
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="approveResultsDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-title>
          Are you sure you want to approve results?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="approveResults"><v-icon>mdi-check</v-icon>Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="candidates.dialog"
      persistent
      max-width="1000px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Select Practitioner To Enroll
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="candidates.dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-title>
          <v-text-field label="Omang Number" class="ma-2" v-model="search.omang" dense outlined hide-details clearable @change="buildTerms('candidates')" @click:clear="clearSearch('omang', 'candidates')" />
          <v-text-field label="Passport Number" class="ma-2" v-model="search.passport" dense outlined hide-details clearable @change="buildTerms('candidates')" @click:clear="clearSearch('passport', 'candidates')" />
        </v-card-title>
        <v-card-subtitle
          v-if="candidates.error_message"
          class="white--text error"
        >{{ candidates.error_message }}</v-card-subtitle>
        <v-card-text>
          {{ candidates.selected.length }} Selected
          <v-data-table
            v-model="candidates.selected"
            style="cursor: pointer"
            :headers="candidates.headers"
            :items="candidates.results"
            item-key="id"
            :options.sync="candidates.options"
            :server-items-length="candidates.total"
            :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
            :loading="candidates.loadingCandidates"
            class="elevation-1"
            show-select
          >
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-btn color="success" :disabled="candidates.selected.length === 0" @click="enroll">Enroll Selected</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-card 
      class="mx-auto"
      max-width="900"
      outlined
      >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        KITSO Scheduled Exam
      </v-card-title>
      <v-card-text class="my-3">
        <v-row>
          <v-col cols="4">
            <v-btn :disabled="!canEnroll" color="secondary" dark small @click="getCandidates(true, 'waitingexam')">
              <v-icon light left>mdi-playlist-check</v-icon>
              <span>Enroll Candidates</span>
            </v-btn>
            <br>
            <br>
            <v-btn :disabled="!canApproveResults" color="success" dark small @click="approveResultsDialog = true">
              <v-icon light left>mdi-check</v-icon>
              <span>Approve Resultss</span>
            </v-btn>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="8">
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Name:</span>
              </v-col>
              <v-col cols="8">
                <v-chip
                  class="ma-2"
                >
                  {{ exam.name }}
                </v-chip>
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Course:</span>
              </v-col>
              <v-col cols="8">
                <v-chip
                  class="ma-2"
                >
                  {{ exam.course.name }}
                </v-chip>
              </v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Start Date:</span>
              </v-col>
              <v-col cols="8">{{ exam.startdate }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">End Date:</span>
              </v-col>
              <v-col cols="8">{{ exam.enddate }}</v-col>
            </v-row>
            <v-divider/>
            <v-row class="justify-space-between">
              <v-col cols="4">
                <span class="font-weight-bold">Status:</span>
              </v-col>
              <v-col cols="8">{{ exam.status.display }}</v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <br>
    <v-card>
      <v-card-title>
        Enrolled Candidates
        <v-spacer></v-spacer>
        <v-btn
          color="error"
          small
          :disabled="candidates.selected.length === 0 || exam.status.code === 'yes'"
          @click="unenroll"
        >
          <v-icon left>mdi-close-outline</v-icon>Un Enroll Selected
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn color="primary" v-if="!resultsMode" @click="activateAddResults">
          <template v-if="exam && exam.status && exam.status.code === 'yes'">
            View Results
          </template>
          <template v-else>
            Add Results
          </template>
        </v-btn>
        <template v-else>
          <v-btn color="success" :disabled="exam && exam.status && exam.status.code === 'yes'" @click="saveResults">Save Results</v-btn>
          &nbsp;&nbsp;&nbsp;
          <v-btn color="primary" @click="deactivateAddResults">Hide Results</v-btn>
        </template>
      </v-card-title>
      <v-card-title>
        <v-text-field label="Omang Number" class="ma-2" v-model="search.omang" dense outlined hide-details clearable @change="buildTerms('enrolled')" @click:clear="clearSearch('omang', 'enrolled')" />
        <v-text-field label="Passport Number" class="ma-2" v-model="search.passport" dense outlined hide-details clearable @change="buildTerms('enrolled')" @click:clear="clearSearch('passport', 'enrolled')" />
      </v-card-title>
      <v-card-subtitle
        v-if="enrolled.error_message"
        class="white--text error"
      >{{ enrolled.error_message }}</v-card-subtitle>
      <v-card-text>
        <v-data-table
          style="cursor: pointer"
          :headers="enrolled.headers"
          :items="enrolled.results"
          item-key="id"
          :options.sync="enrolled.options"
          :server-items-length="enrolled.total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="enrolled.loadingEnrolled"
          class="elevation-1"
          show-select
          v-model="candidates.selected"
        >
          <template v-slot:item="{ item }">
            <tr @click="clickIt(item)">
              <td>
                <v-checkbox
                  @click.stop
                  v-model="candidates.selected"
                  color="red"
                  :value="item"
                  hide-details
                  :disabled="exam.status.code === 'yes'"
                ></v-checkbox>
              </td>
              <td>{{ item.surname }}</td>
              <td>{{ item.given }}</td>
              <td>{{ item.birthDate }}</td>
              <td>{{ item.gender }}</td>
              <td>{{ item.omang[0] }}</td>
              <td>{{ item.passport[0] }}</td>
              <template v-if="resultsMode">
                <template v-if="exam.mode == 'score'">
                  <td v-for="assessment in exam.assessments" :key="item.id+assessment.code">
                    <v-text-field
                      @click.stop
                      :readonly="exam && exam.status && exam.status.code === 'yes'"
                      :error-messages="examResults[item.examId][assessment.code].errors"
                      v-model="examResults[item.examId][assessment.code].score"
                      outlined
                      hide-details="auto"
                      dense
                      @input="validateScore(item.examId)"
                    />
                  </td>
                </template>
                <td v-else-if="exam.mode == 'noscore'">
                  <v-select
                    @click.stop
                    :readonly="exam && exam.status && exam.status.code === 'yes'"
                    :items="scoreAssessment"
                    v-model="examResults[item.examId]['marks'].score"
                    label="Score"
                    @input="validateScore(item.examId)"
                    clearable
                  ></v-select>
                </td>
                <td>
                  <v-chip @click.stop dark color="green" v-if="examResults[item.examId]['exam-assessment'] === 'Pass'">
                    {{ examResults[item.examId]["exam-assessment"] }}
                  </v-chip>
                  <v-chip @click.stop dark color="red" v-if="examResults[item.examId]['exam-assessment'] === 'Fail' || examResults[item.examId]['exam-assessment'] === 'Incomplete'">
                    {{ examResults[item.examId]["exam-assessment"] }}
                  </v-chip>
                </td>
              </template>
            </tr>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      resultsMode: false,
      approveResultsDialog: false,
      registrationDate: "",
      registrationDateFormatted: "",
      registrationDateMenu: false,
      exam: {
        enddate: "",
        status: {},
        course: {},
        assessments: []
      },
      examResults: {},
      search: {
        name: '',
        omang: '',
        passport: ''
      },
      enrolled: {
        options: { itemsPerPage: 5 },
        total: 0,
        prevPage: -1,
        link: [],
        terms: {},
        results: [],
        headers: [{
          text: "Surname",
          value: "surname"
        }, {
          text: "Given Name(s)",
          value: "given"
        }, {
          text: "Birth Date",
          value: "birthDate"
        }, {
          text: "Gender",
          value: "gender"
        }, {
          text: "OMANG",
          value: "omang"
        }, {
          text: "Passport",
          value: "passport"
        }],
        loadingEnrolled: false,
        update_again: { rerun: false, restart: false },
        error_message: null,
      },
      candidates: {
        selected: [],
        dialog: false,
        options: { itemsPerPage: 10 },
        total: 0,
        prevPage: -1,
        link: [],
        terms: {},
        results: [],
        headers: [{
          text: "Surname",
          value: "surname"
        }, {
          text: "Given Name(s)",
          value: "given"
        }, {
          text: "Birth Date",
          value: "birthDate"
        }, {
          text: "Gender",
          value: "gender"
        }, {
          text: "OMANG",
          value: "omang"
        }, {
          text: "Passport",
          value: "passport"
        }],
        loadingCandidates: false,
        update_again: { rerun: false, restart: false },
        error_message: null,
      },
      applicantStatus: '',
      scoreAssessment: [{
        text: "Pass",
        value: "pass"
      }, {
        text: "Fail",
        value: "fail"
      }, {
        text: "Incomplete",
        value: "incomplete"
      }]
    }
  },
  watch: {
    'candidates.options': {
      handler() {
        this.getCandidates();
      },
      deep: true
    },
    'enrolled.options': {
      handler() {
        this.getEnrolled();
      },
      deep: true
    }
  },
  computed: {
    canEnroll() {
      // if(!this.hasTask('kitso-exam-scheduling')) {
      //   return false
      // }
      if(!this.exam.enddate || this.$moment(this.exam.enddate).isBefore(this.$moment().format("YYYY-MM-DD"))) {
        return false
      }
      return true
    },
    canApproveResults() {
      // if(!this.hasTask('kitso-exam-approval')) {
      //   return false
      // }
      if(this.exam && this.exam.status && this.exam.status.code === 'yes') {
        return false
      }
      return true
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    selectAll() {
      this.candidates.selected = this.candidates.selected.concat(this.candidates.results)
    },
    unselectDisabled(item) {
      if(item.disabled) {
        let index = this.candidates.selected.findIndex((selelected) => {
          return selelected.id === item.id
        })
        if(index > -1) {
          this.candidates.selected.splice(index, 1)
        }
      }
    },
    approveResults() {
      // if(!this.hasTask('kitso-practitioner-exam-profile')) {
      //   return
      // }
      this.approveResultsDialog = false
      this.overlay = true
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          exam: this.queries.exam,
          registrationDate: this.registrationDate
        })
      }
      fetch("/site/kitso/approveResults", opts).then((response) => {
        this.overlay = false;
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Results approved successfully",
          });
          this.getExam()
          this.getEnrolled()
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to generate registrations for some few candidates, try again."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to generate registrations for some few candidates, try again."
        });
      })
    },
    clickIt: function(record) {
      this.$router.push({
        path: "/custom/kitso-person-exam?schedule=" + this.queries.exam + "&practitioner=" + record.id + "&path=/components/kitso/"
      })
    },
    validateScore(id) {
      let total = 0
      let errors = false
      let complete = true
      if(this.exam.mode === 'noscore') {
        let score = this.examResults[id]["marks"].score
        if(score && score == 'pass') {
          this.examResults[id]["exam-assessment"] = "Pass"
        } else if(score && score == 'fail') {
          this.examResults[id]["exam-assessment"] = "Fail"
        } else if(score && score == 'incomplete') {
          this.examResults[id]["exam-assessment"] = "Incomplete"
        }
        return
      }
      for(let assessment of this.exam.assessments) {
        this.examResults[id][assessment.code].errors = []
        let score = this.examResults[id][assessment.code].score
        if(score && score.toString().split(".").length > 1 && !score.toString().split(".")[1]) {
          this.examResults[id][assessment.code].errors = ["Incomplete number"]
          errors = true
        }
        if(isNaN(score)) {
          this.examResults[id][assessment.code].errors = ["Must be a number"]
          errors = true
          break
        }
        if(score) {
          total += parseFloat(score)
        } else {
          complete = false
        }
        if(total > 100) {
          this.examResults[id][assessment.code].errors = ["Total Exceeds 100"]
          errors = true
        }
      }
      if(errors || !complete) {
        this.examResults[id]["exam-assessment"] = ""
      } else if(this.exam.course.passmark > total) {
        this.examResults[id]["exam-assessment"] = "Fail"
      } else if(this.exam.course.passmark <= total) {
        this.examResults[id]["exam-assessment"] = "Pass"
      }
    },
    activateAddResults() {
      this.resultsMode = true
      for(let assessment of this.exam.assessments) {
        this.enrolled.headers.push({
          text: assessment.display,
          value: assessment.code
        })
      }
      this.enrolled.headers.push({
        text: "Assessment",
        value: "exam-assessment"
      })
    },
    deactivateAddResults() {
      this.resultsMode = false
      for(let assessment of this.exam.assessments) {
        let index = this.enrolled.headers.findIndex((header) => {
          return header.value === assessment.code
        })
        this.enrolled.headers.splice(index, 1)
      }
      let index = this.enrolled.headers.findIndex((header) => {
        return header.value === 'exam-assessment'
      })
      this.enrolled.headers.splice(index, 1)
    },
    saveResults() {
      // if(!this.hasTask('kitso-practitioner-exam-profile')) {
      //   return
      // }
      this.examResults.mode = this.exam.mode
      this.overlay = true
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(this.examResults)
      }
      fetch("/site/kitso/saveResults", opts).then((response) => {
        this.overlay = false;
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Results saved successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to save results."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to save results."
        });
      })
    },
    enroll() {
      this.candidates.dialog = false
      this.overlay = true;
      let bundle = {
        resourceType: "Bundle",
        type: "transaction",
        entry: []
      }
      for(let candidate of this.candidates.selected) {
        let resource = {
          resourceType: "Basic",
          meta: {
            profile: ["http://ihris.org/fhir/StructureDefinition/kitso-practitioner-exam-profile"]
          },
          extension: [{
            url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
            valueReference: {
              reference: "Practitioner/" + candidate.id
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/kitso-exam-scheduling-reference",
            valueReference: {
              reference: "Basic/" + this.queries.exam
            }
          }]
        }
        bundle.entry.push({
          resource,
          request: {
            method: "POST",
            url: "Basic"
          }
        })
      }
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(bundle)
      }
      fetch("/site/kitso/enrollCandidates", opts).then((response) => {
        this.candidates.selected = []
        this.overlay = false;
        if(response.status === 201 || response.status === 200) {
          this.getEnrolled()
          this.$store.commit("setMessage", {
            type: "success",
            text: "Candidates Enrolled Successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to enroll candidates."
          });
        }
      }).catch(() => {
        this.candidates.selected = []
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to enroll candidates."
        });
      })
    },
    unenroll() {
      this.overlay = true;
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          candidates: this.candidates.selected,
          exam: this.queries.exam
        })
      }
      fetch("/site/kitso/unenrollCandidates", opts).then((response) => {
        this.overlay = false;
        this.candidates.selected = []
        if(response.status === 201 || response.status === 200) {
          this.getEnrolled()
          this.$store.commit("setMessage", {
            type: "success",
            text: "Candidates Un Enrolled Successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to un enroll candidates."
          });
        }
      }).catch(() => {
        this.candidates.selected = []
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to un enroll candidates."
        });
      })
    },
    getExam() {
      fetch("/fhir/Basic/" + this.queries.exam).then((response) => {
        response.json().then(async(exam) => {
          let assessments = exam.extension.filter((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/kitso-exam-assessment-type'
          })
          this.exam.assessments = []
          for(let assessment of assessments) {
            this.exam.assessments.push({
              code: assessment.valueCoding.code,
              display: assessment.valueCoding.display
            })
          }
          this.exam.name = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-basic-name'
          }).valueString
          this.exam.startdate = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/start-date'
          }).valueDate
          this.exam.enddate = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/end-date'
          }).valueDate
          this.exam.mode = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-mode-of-assessment'
          })?.valueCoding?.code
          let course = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-training-course'
          })?.valueReference?.reference
          await fetch("/fhir/Basic/" + course.split("/")[1]).then((response) => {
            response.json().then((response) => {
              this.exam.course.name = response.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-basic-name'
              }).valueString
              this.exam.course.passmark = response.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/training-course-pass-mark'
              }).valueDecimal
            })
          })
          let status = exam.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-course-evaluated'
          })
          if(status) {
            if(status.valueCoding.code === 'yes') {
              status.valueCoding.display = "Evaluated and Approved"
            } else {
              status.valueCoding.display = "Evaluation in Progress"
              status.valueCoding.code === 'no'
            }
            this.exam.status = status.valueCoding
          }
        })
      })
    },
    clearSearch: function(field, category) {
      this.search[field] = ""
      this[category].terms.practitioner = ''
      this.buildTerms(category)
    },
    checkRerunCandidates() {
      if ( !this.candidates.loadingCandidates && this.candidates.update_again.rerun ) {
        this.getCandidates( this.candidates.update_again.restart )
        this.candidates.update_again = { rerun: false, restart: false }
      }
    },
    checkRerunEnrolled() {
      if ( !this.enrolled.loadingEnrolled && this.enrolled.update_again.rerun ) {
        this.getEnrolled( this.enrolled.update_again.restart )
        this.enrolled.update_again = { rerun: false, restart: false }
      }
    },
    async buildTerms(category) {
      if(category === "candidates") {
        this.getCandidates(true);
      } else if(category === "enrolled") {
        this.enrolled.terms = {}
        await this.getDataPractitioner(category)
        this.getEnrolled(true);
      }
    },
    getDataPractitioner(category) {
      return new Promise((resolve, reject) => {
        let identifier = ""
        if(this.search.omang) {
          identifier = "?identifier=" + this.search.omang
        }
        if(this.search.passport) {
          if(identifier) {
            identifier += "&identifier=" + this.search.passport
          } else {
            identifier = "?identifier=" + this.search.passport
          }
        }
        if(!identifier) {
          return resolve()
        }
        let url = "/fhir/Practitioner" + identifier
        fetch(url).then(response => {
          response.json().then(async (data) => {
            let practitioners = []
            if(data.entry && data.entry.length > 0) {
              for (let entry of data.entry) {
                practitioners.push("Practitioner/" + entry.resource.id)
              }
            }
            if(practitioners.length > 0) {
              this[category].terms["practitioner"] = practitioners.join(",")
            } else {
              this[category].terms["practitioner"] = "Practitioner/fakeprct"
            }
            resolve()
          }).catch(() => {
            reject()
          })
        }).catch(() => {
          reject()
        })
      })
    },
    getCandidates(restart, status) {
      if(status) {
        this.applicantStatus = status
      }
      if(!this.candidates.dialog) {
        this.candidates.selected = []
      }
      this.candidates.dialog = true
      if ( this.candidates.loadingCandidates ) {
        this.candidates.update_again.rerun = true
        this.candidates.update_again.restart = this.candidates.update_again.restart || restart
        return
      }
      this.candidates.loadingCandidates = true;
      this.candidates.error_message = null;
      let url = "";
      if (restart) this.candidates.options.page = 1;
      if (this.candidates.options.page > 1) {
        if (this.candidates.options.page === this.candidates.prevPage - 1) {
          url = this.candidates.link.find(link => link.relation === "previous").url;
        } else if (this.candidates.options.page === this.candidates.prevPage + 1) {
          url = this.candidates.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace("/fhir?","/fhir/Practitioner?")
        url = url.substring(url.indexOf("/fhir/"));

        
        if(url.indexOf('_sort=-_lastUpdated') === -1) {
          url = url + '&_sort=-_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.candidates.options.itemsPerPage;
        let sort = "";
        for (let idx in this.candidates.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.candidates.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.candidates.options.sortBy[idx];
        }
        url = "/fhir/Practitioner?&_count=" + count + "&_sort=-_lastUpdated&_total=accurate"
      }
      let identifier = ""
      if(this.search.omang) {
        identifier = "&identifier=" + this.search.omang
      }
      if(this.search.passport) {
        if(identifier) {
          identifier += "&identifier=" + this.search.passport
        } else {
          identifier = "?identifier=" + this.search.passport
        }
      }
      url += identifier
      this.candidates.prevPage = this.candidates.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.candidates.results = [];
          if (data.total > 0) {
            this.candidates.link = data.link;
            for (let entry of data.entry) {
              let result = { id: entry.resource.id };
              result.surname = entry.resource.name[0].family
              result.given = entry.resource.name[0].given.join(", ")
              result.birthDate = entry.resource.birthDate
              result.gender = entry.resource.gender
              result.gender = await this.$fhirutils.lookup( result.gender, 'http://hl7.org/fhir/administrative-gender' )
              result.omang = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='omang').value" );
              result.passport = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='passport').value" );
              this.candidates.results.push(result);
            }
          }
          this.candidates.total = data.total;
          this.candidates.loadingCandidates = false;
          this.checkRerunCandidates()
        }).catch(err => {
          this.candidates.loadingCandidates = false;
          this.candidates.error_message = "Unable to load results.";
          this.checkRerunCandidates()
          console.log(err);
        });
      }).catch(err => {
        this.candidates.loadingCandidates = false;
        this.candidates.error_message = "Unable to load results.";
        this.checkRerunCandidates()
        console.log(err);
      });
    },
    getEnrolled(restart) {
      this.enrolled.selected = []
      this.enrolled.dialog = true
      if ( this.enrolled.loadingEnrolled ) {
        this.enrolled.update_again.rerun = true
        this.enrolled.update_again.restart = this.enrolled.update_again.restart || restart
        return
      }
      this.enrolled.loadingEnrolled = true;
      this.enrolled.error_message = null;
      let url = "";
      if (restart) this.enrolled.options.page = 1;
      if (this.enrolled.options.page > 1) {
        if (this.enrolled.options.page === this.enrolled.prevPage - 1) {
          url = this.enrolled.link.find(link => link.relation === "previous").url;
        } else if (this.enrolled.options.page === this.enrolled.prevPage + 1) {
          url = this.enrolled.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace("/fhir?","/fhir/Basic?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_profile=http://ihris.org/fhir/StructureDefinition/kitso-practitioner-exam-profile') === -1) {
          url = url + '&_profile=http://ihris.org/fhir/StructureDefinition/kitso-practitioner-exam-profile'
        }
        if(url.indexOf('kitsoscheduledexam=Basic/' + this.queries.exam) === -1) {
          url = url + '&kitsoscheduledexam=Basic/' + this.queries.exam
        }
        if(url.indexOf('_include=Basic:practitioner') === -1) {
          url = url + '&_include=Basic:practitioner'
        }
        if(url.indexOf('_sort=-_lastUpdated') === -1) {
          url = url + '&_sort=-_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.enrolled.options.itemsPerPage;
        let sort = "";
        for (let idx in this.enrolled.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.enrolled.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.enrolled.options.sortBy[idx];
        }
        url =
          "/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/kitso-practitioner-exam-profile" + 
          "&kitsoscheduledexam=Basic/" + this.queries.exam + 
          "&_include=Basic:practitioner" + 
          "&_count=" + count +
          "&_sort=-_lastUpdated&_total=accurate"
      }
      let sTerms = Object.keys(this.enrolled.terms);
      for (let term of sTerms) {
        if ( Array.isArray( this.enrolled.terms[term] ) ) {
          if ( this.enrolled.terms[term].length > 0 ) {
            url += "&" + term + "=" + this.enrolled.terms[term].join(',')
          }
        } else if ( this.enrolled.terms[term] ) {
          url += "&" + term + "=" + this.enrolled.terms[term];
        }
      }

      this.enrolled.prevPage = this.enrolled.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.enrolled.results = [];
          if (data.total > 0) {
            this.enrolled.link = data.link;
            for (let entry of data.entry) {
              if(entry.resource.resourceType !== 'Practitioner') {
                continue
              }
              let result = { id: entry.resource.id };
              result.surname = entry.resource.name[0].family
              result.given = entry.resource.name[0].given.join(", ")
              result.birthDate = entry.resource.birthDate
              result.gender = entry.resource.gender
              result.gender = await this.$fhirutils.lookup( result.gender, 'http://hl7.org/fhir/administrative-gender' )
              result.omang = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='omang').value" );
              result.passport = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='passport').value" );
              for (let entry of data.entry) {
                if(!entry.resource.extension) {
                  continue
                }
                let practitioner = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
                })?.valueReference?.reference
                if('Practitioner/' + result.id !== practitioner) {
                  continue
                }
                if(entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/kitso-practitioner-exam-profile")) {
                  result.examId = entry.resource.id
                  let score
                  if(this.exam.mode === 'score') {
                    if(!this.examResults[entry.resource.id]) {
                      this.$set(this.examResults, entry.resource.id, {})
                    }
                    for(let assessment of this.exam.assessments) {
                      let score = ""
                      for(let ext of entry.resource.extension) {
                        if(ext.url === "http://ihris.org/fhir/StructureDefinition/" + assessment.code + "-score") {
                          score = ext.valueDecimal
                          break
                        }
                      }
                      if(score && !this.examResults[entry.resource.id][assessment.code]) {
                        this.$set(this.examResults[entry.resource.id], assessment.code, {score: score, errors: []})
                      } else if(!score && !this.examResults[entry.resource.id][assessment.code]) {
                        this.$set(this.examResults[entry.resource.id], assessment.code, {score: "", errors: []})
                      }
                    }
                  } else if(this.exam.mode === 'noscore') {
                    score = entry.resource.extension.find((ext) => {
                      return ext.url === 'http://ihris.org/fhir/StructureDefinition/passfail-score'
                    })?.valueCoding?.code
                    if(!score) {
                      score = ""
                    }
                    this.$set(this.examResults, entry.resource.id, {})
                    this.$set(this.examResults[entry.resource.id], "marks", {score: score, errors: []})
                  }
                  let assessment = entry.resource.extension.find((ext) => {
                    return ext.url === 'http://ihris.org/fhir/StructureDefinition/exam-assessment'
                  })
                  if(assessment && !this.examResults[entry.resource.id]["exam-assessment"]) {
                    this.$set(this.examResults[entry.resource.id], "exam-assessment", assessment.valueCoding.display)
                  } else if(!assessment && !this.examResults[entry.resource.id]["exam-assessment"]){
                    this.$set(this.examResults[entry.resource.id], "exam-assessment", "")
                  }
                }
              }
              this.enrolled.results.push(result);
            }
          }
          this.enrolled.total = data.total;
          this.enrolled.loadingEnrolled = false;
          this.checkRerunEnrolled()
        }).catch(err => {
          this.enrolled.loadingEnrolled = false;
          this.enrolled.error_message = "Unable to load results.";
          this.checkRerunEnrolled()
          console.log(err);
        });
      }).catch(err => {
        this.enrolled.loadingEnrolled = false;
        this.enrolled.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    },
    hasTask(task) {
      return this.$store.state.user.obj.permissions && this.$store.state.user.obj.permissions.special && this.$store.state.user.obj.permissions.special.special && this.$store.state.user.obj.permissions.special.special.id && this.$store.state.user.obj.permissions.special.special.id[task] ||
            this.$store.state.user.obj.permissions && this.$store.state.user.obj.permissions.special && this.$store.state.user.obj.permissions.special.navigation && this.$store.state.user.obj.permissions.special.navigation.id && this.$store.state.user.obj.permissions.special.navigation.id[task]

    }
  },
  created() {
    this.getExam()
    this.getEnrolled()
  }
}
</script>